import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"
import prompt from '../../components/prompt.vue';
import config from "config/config.js";

export default {
	data() {
		return {
			userName: '',
			userIconImg: '',
			quizList: [],
			blank: " ",
			drawerVisible: false,
			ishidden: true,
			allUsers: [],
		}
	},
	components: {
		uniDrawer,
		prompt
	},

	onLoad() {
		let that = this;
	},
	onShow() {
		this.getData();
		this.allUsers = getApp().globalData.allUsers;
	},

	// 下拉刷新
	onPullDownRefresh() {
		// console.log('refresh');
		uni.showNavigationBarLoading();
		this.getData();
	},

	methods: {

		updateInfo() {
			this.drawerVisible = true;
		},

		closeDrawer() {
			this.drawerVisible = false
		},

		modifyIcon() {
			let that = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				success: function(res) {
					console.log("chooseImage---res:", res);
					console.log("chooseImage:", res.tempFilePaths[0]);
					that.userIconImg = res.tempFilePaths[0];
				}
			});
		},

		modifyName() {
			this.ishidden = false;
		},

		logOut() {
			uni.reLaunch({
				url: '../login/index'
			})
		},

		//提示框
		prompt: function() {
			this.$refs.prompt.show();
		},
		onConfirm: function(e) {
			console.log(e);
			let _cost = e;
			var nameRE = /^[\u4e00-\u9fa5_A-Za-z0-9]{3,8}$/;
			var norepeat = true;
			var nameFlag = nameRE.test(_cost);
			for (var i = 0; i < this.allUsers.length; i++) {
				if (_cost == this.allUsers[i].uname) {
					norepeat = false;
					break;
				}
			}
			if (_cost == '') {
				uni.showModal({
					title: '提示',
					content: '您还未输入',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (!nameFlag) {
				uni.showModal({
					title: '提示',
					content: '用户名不符合规范',
					showCancel: false,
					confirmText: "确定"
				});
			} else if (!norepeat) {
				uni.showModal({
					title: '提示',
					content: '用户名重复，请重新输入',
					showCancel: false,
					confirmText: "确定"
				});
			} else {
				// this.$refs.prompt.hide();
				this.ishidden = true;
				uni.showModal({
					title: '提示',
					content: '你输入的是:' + _cost,
					showCancel: false,
					confirmText: "确定"
				});
				this.userName = _cost;
			}
		},
		onCancel: function() {
			// this.$refs.prompt.hide();
			this.ishidden = true;
			this.$refs.prompt.cost = '';
		},


		getData() {
			this.userName = getApp().globalData.userName;
			this.userIconImg = getApp().globalData.userIconImg;
			let that = this;
			uni.request({
				url: config.reUrl + '/FuStar/QueryAllQuestion',
				data: {

				},
				success: (res) => {
					// console.log("QueryAllQuestion:", res.data);
					getApp().globalData.questionInfo = res.data;
					// console.log("addQuestion -- global -- questionInfo:", getApp().globalData.questionInfo);
					that.quizList = [];
					if (res.data != null) {
						for (let i = 0; i < res.data.length; i++) {
							if (res.data[i].quizUserName == that.userName) {
								that.quizList.push(res.data[i]);
							}
						}
					}
					// that.quizList.reverse();
					uni.stopPullDownRefresh();
					uni.hideNavigationBarLoading()
				}
			});
		}
	}

}
